<template>
	<el-dialog
		:modal="modal"
		:visible.sync="dialog"
		:center="ture"
		:modal-append-to-body="false"
		:close-on-click-modal="false"
		custom-class="dialog-normal"
		:show-close="false"
		ref="plotlist"
		:top="dialogTop"
		:destroy-on-close="true"
	>
		<div class="border" v-for="index in 4" :key="index"></div>
		<!--弹出层关闭按钮-->
		<div class="popclose" @click="dialog = false">
			<i class="iconfont icon-guanbi2"></i>
		</div>
		<div class="dialog-con" v-loading="loading" element-loading-text="数据加载中..." element-loading-spinner="el-icon-loading" element-loading-background="rgba(0, 0, 0, 0)">
			<div class="dialog-body">
				<slot v-show="loading"></slot>
			</div>
		</div>
	</el-dialog>
</template>
<script>
export default {
	data() {
		return {};
	},
	props: {
		dialogVisible: {
			default: false,
			type: Boolean
		},
		title: {
			default: '',
			type: String
		},
		loading: {
			default: false,
			type: Boolean
		},
		dialogTop: {
			default: '15vh',
			type: String
		},
		modal: {
			default: true,
			type: Boolean
		}
	},
	computed: {
		dialog: {
			get() {
				return this.dialogVisible;
			},
			set(val) {
				this.$emit('update:dialogVisible', val);
			}
		}
	}
};
</script>

<style scoped lang="scss">
[v-cloak] {
	display: none;
}
.border {
	position: absolute;
	background-size: 100% 100% !important;
	&:nth-child(1) {
		width: 65px;
		height: 36px;
		top: -1px;
		left: -1px;
		background: url('~@/assets/images/common/box-border-top-left-bg.png') no-repeat;
	}
	&:nth-child(2) {
		width: 15px;
		height: 48px;
		top: -1px;
		right: -1px;
		background: url('~@/assets/images/common/box-border-top-right-bg.png') no-repeat;
	}
	&:nth-child(3) {
		width: 97px;
		height: 18px;
		bottom: -1px;
		right: -1px;
		background: url('~@/assets/images/common/box-border-bottom-right-bg.png') no-repeat;
	}
	&:nth-child(4) {
		width: 45px;
		height: 59px;
		bottom: -1px;
		left: -1px;
		background: url('~@/assets/images/common/box-border-bottom-left-bg.png') no-repeat;
	}
}
/deep/ .el-loading-spinner {
	.el-icon-loading {
		font-size: 42px;
		color: #00f6ff;
	}
	p.el-loading-text {
		font-size: 16px;
		color: #00f6ff;
	}
}
/deep/ .dialog-normal {
	// width: 1152px;
	height: 622px;
	border: 1px solid #10a4a9;
	padding: 15px 10px 4px;
	box-sizing: border-box;
	box-shadow: 0 0 50px -15px rgba(0, 246, 255, 0.8);
	background: transparent;
	margin-bottom: 0;
	.el-dialog__header {
		display: none;
	}
	.el-dialog__body {
		width: 100%;
		height: 100%;
		box-sizing: border-box;
		padding: 0;
		background: rgba(5, 37, 58, 0.9);
		.popclose {
			width: 36px;
			height: 36px;
			z-index: 9999;
			position: absolute;
			top: -45px;
			right: -57px;
			cursor: pointer;
			border: 2px solid #00f6ff;
			border-radius: 50%;
			display: flex;
			justify-content: center;
			align-items: center;
			i {
				font-size: 20px;
				color: #00f6ff;
				margin-right: 0;
			}
		}
		.dialog-con {
			width: 100%;
			height: 100%;
			margin-top: -5px;
			overflow: hidden;
			.dialog-body {
				overflow: hidden;
				height: 100%;
			}
		}
	}
}
</style>



